import React, { Component } from "react";

class Gameplay extends Component {
    constructor(props) {
        super(props);
        this.state = {
            wordCount: 10,
        };

        // constant for words used in this app. 100 words.
        this.demowords = ['Lorem', 'ipsum', 'dolor', 'sit', 'amet,', 'consectetur', 'adipisicing', 'elit.', 'Aliquid,', 'dignissimos', 'facere', 'necessitatibus', 'obcaecati', 'odit', 'officia', 'quam', 'quasi', 'reprehenderit', 'similique', 'vel', 'velit', 'vitae.', 'Culpa', 'doloremque', 'modi', 'nisi', 'repellendus?', 'Dignissimos,', 'harum,', 'numquam?', 'A', 'ad', 'blanditiis', 'cum', 'cupiditate', 'est,', 'minus', 'natus', 'placeat,', 'qui', 'quia', 'sequi', 'veritatis', 'voluptatem?', 'Aspernatur', 'assumenda', 'blanditiis', 'consequatur', 'debitis', 'enim', 'eos,', 'eveniet', 'exercitationem', 'labore', 'mollitia', 'neque', 'quaerat', 'recusandae', 'ut', 'velit!', 'Aspernatur,', 'cupiditate', 'dolore', 'eligendi', 'enim', 'ipsum', 'iste', 'labore', 'laboriosam', 'magni', 'modi', 'necessitatibus', 'obcaecati', 'placeat', 'quasi', 'qui', 'quod', 'soluta', 'sunt', 'ut?', 'Doloribus', 'explicabo', 'ipsum', 'non', 'quibusdam', 'sequi', 'sint', 'veniam', 'veritatis', 'vitae!', 'Aperiam', 'incidunt', 'iusto', 'reiciendis', 'repellendus', 'veritatis.', 'Ab', 'accusamus', 'alias', 'aperiam'];
    }

    wordCountChange(wordCount) {
        this.setState({
            wordCount: wordCount,
        })
    }

    render() {
        const wordCountMode = [10, 25, 50, 100];
        return (
            <div className="container-fluid">
                <nav aria-label="word-count">
                    <ul className="pagination">
                        {
                            wordCountMode.map(el => {
                                return (
                                    <li className="page-item">
                                        <button className="page-link"
                                                key={el}
                                                onClick={() => this.wordCountChange(el)}
                                                type="button">{el}</button>
                                    </li>
                                );
                            })
                        }
                    </ul>
                </nav>
                <textarea name="word-display-area" id="word-display-area" cols="30" rows="5" className="form-control">
                    {
                        this.demowords.slice(0, this.state.wordCount).map((el, ind) => {
                            return el;
                        })
                    }
                </textarea>
                <input type="text" name="word-input" id="word-input" className="form-control" />
            </div>
        );
    }
}

export default Gameplay;